.slick-arrow {
    background: transparent linear-gradient(202deg, #78539C 0%, #6D3085 50%, #393185 100%) 0% 0% no-repeat padding-box !important;
    height: 90px !important;
    width: 40px !important;
    display: flex !important;
    z-index: 20000;
    top: 30% !important;
    font-size: 14px !important;
}

.slick-arrow::before {
    margin: auto;
    color: white !important;
}

.slick-arrow:hover {
    background: transparent linear-gradient(202deg, #78539C 0%, #6D3085 50%, #393185 100%) 0% 0% no-repeat padding-box !important;
}

.slick-arrow.slick-prev {
    left: 12px;
}

.slick-arrow.slick-next {
    right: 12px;
}

.slick-arrow.slick-prev::before {
    content: "❮";
}

.slick-arrow.slick-next::before {
    content: "❯";
}

.home-page-carasoul .slick-arrow {
    background: transparent linear-gradient(202deg, #78539C 0%, #6D3085 50%, #393185 100%) 0% 0% no-repeat padding-box !important;
    height: 146px !important;
    width: 59px !important;
    display: flex !important;
    z-index: 20000;
    top: 30% !important;
}

.product-detail-slider-dashboard .slick-arrow {
    height: 20px !important;
    width: 20px !important;
    left: 50px;
    background: none !important;
    font-size: 20px !important;
}

.product-detail-slider-dashboard .slick-arrow.slick-prev {
    top: -4% !important;
}

.product-detail-slider-dashboard .slick-arrow.slick-next {
    top: 104% !important;
}

.product-detail-slider-dashboard .slick-arrow.slick-prev::before {
    content: "❮";
    color: black !important;
    transform: rotate(90deg);
}

.product-detail-slider-dashboard .slick-arrow.slick-next::before {
    color: black !important;
    content: "❯";
    transform: rotate(90deg);
}


