body {
  margin: 0;
}

.app-gradient {
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box !important;
  color: white;
}

a {
  color: #000 !important;
}

.horizintal-category-menu {
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box !important;
  color: white;
}

.ant-menu-item-group-list span {
  color: black !important;
}

.horizintal-category-menu li {
  padding: 0 20px !important;
}

.horizintal-category-menu li.ant-menu-item span,
.horizintal-category-menu li.ant-menu-overflow-item span {
  color: white !important;
  font-size: 30px;
}

.app-modal-body-overflow .ant-modal-body,
.app-modal-without-footer-body-overflow .ant-modal-body {
  max-height: calc(100vh - 200px) !important;
  display: flex;
  flex-direction: column;
  overflow: auto !important;
}

.app-modal-without-footer-body-overflow .ant-modal-body {
  max-height: calc(100vh - 100px) !important;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #F1F1F1;
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(133, 112, 112);
}